import { createApp } from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import BootstrapVue3 from "bootstrap-vue-3";
import "bootstrap/dist/css/bootstrap.css";
import "bootstrap-vue-3/dist/bootstrap-vue-3.css";
// 전역으로 등록할 컴포넌트를 import
import status from "./components/status.vue";
import newPage from "@/components/newPage";

import headerT from "@/components/layout01/headerT";
import sectionB from "@/components/layout01/sectionB";
import articleB from "@/components/layout01/articleB";
import footerB from "@/components/layout01/footerB";

import headerT02 from "@/components/layout02/headerT02";
import sectionB02 from "@/components/layout02/sectionB02";
import articleB02 from "@/components/layout02/articleB02";
import footerB02 from "@/components/layout02/footerB02";
import asideB02 from "@/components/layout02/asideB02";

import headerT03 from "@/components/layout03/headerT03";
import sectionB03 from "@/components/layout03/sectionB03";
import articleB03 from "@/components/layout03/articleB03";
import footerB03 from "@/components/layout03/footerB03";
import asideB03 from "@/components/layout03/asideB03";

import index from "@/components/bootstrapVue/Index";
// 컴포넌트 등록
const app = createApp(App);
// app.component('사용시 호출할 이름', 등록할 컴포넌트)
app.component("status", status);
app.component("newPage", newPage);

app.component("headerT", headerT);
app.component("sectionB", sectionB);
app.component("articleB", articleB);
app.component("footerB", footerB);

app.component("headerT02", headerT02);
app.component("sectionB02", sectionB02);
app.component("articleB02", articleB02);
app.component("footerB02", footerB02);
app.component("asideB02", asideB02);

app.component("headerT03", headerT03);
app.component("sectionB03", sectionB03);
app.component("articleB03", articleB03);
app.component("footerB03", footerB03);
app.component("asideB03", asideB03);

app.component("index", index);
// app.mount('#app')

// createApp(App).use(store).use(router).mount("#app");
app.use(store).use(BootstrapVue3).use(router).mount("#app");
