<script>
export default {
  components: {},
  data() {
    return {};
  },
  setup() {},
  created() {},
  mounted() {},
  unmounted() {},
  methods: {
    Add1(event) {
      // 버튼 Add to Cart의 상위노드
      const thisNode = event.target.parentNode;

      // 아이템 선택전 현재 총 가격
      let crntTotPrice = 0;
      const chPrice = document.querySelector("#price");
      console.log(chPrice.textContent.split("$"));
      crntTotPrice = chPrice.textContent.split("$")[1];
      // 선택한 아이템의 가격
      let price = thisNode.childNodes[2].textContent.split("$")[1];

      // 선택한 아이템들의 총 합계
      let itemTotalSum = 0;
      itemTotalSum = Number(crntTotPrice) + Number(price);
      itemTotalSum = itemTotalSum.toFixed(2); // 소수점 2자리까지만 표시

      // console.log("아이템 선택전 현재 총 가격 : " + crntTotPrice);
      // console.log("선택한 아이템의 가격 : " + price);
      // console.log("선택한 아이템들의 총 합계 : " + itemTotalSum);

      // 총합계를 #price에 출력
      chPrice.textContent = "$" + itemTotalSum.toString();
    },
  },
};
</script>

